import React, { useEffect, useState } from 'react';
import { useParams, useHistory, Link } from 'react-router-dom';
import { useSelector, useDispatch } from 'react-redux';
import { getMovie, pageViewInc } from '../store/movies';
import { arrayToString, extractVideoId, helperFunction } from '../helper';
import { ReactComponent as Back } from '../assets/icons2/mdi_arrow-left.svg';
import { getWatchlist } from '../store/user';
import Loading from './Loading';
import poster from '../assets/images/poster.jpg';
import Message from './Message';
import Watchlist from './Watchlist';
import { supabase } from '../lib/api';

const MoviePage = () => {
  let { id } = useParams();
  let history = useHistory();
  const dispatch = useDispatch();

  const back = () => history.goBack();

  // eslint-disable-next-line
  const [helperText, setHelperText] = useState({
    error: null,
    text: null,
    type: null,
  });

  const { data: movieData, loading } = useSelector(
    (state) => state.movies.movie
  );
  const { data: login } = useSelector((state) => state.user.login);
  const { data: watchlist } = useSelector((state) => state.user.watchlist);

  const handleRemoveToWatchlist = async (movie_id) => {
    if (login && movieData) {
      const { error } = await supabase
        .from('watchlist')
        .delete()
        .eq('movie_id', movie_id)
        .eq('user_id', login.id);
      if (error) {
        helperFunction(
          false,
          error.message,
          'alert-warning',
          setHelperText,
          3000
        );
      } else {
        helperFunction(
          false,
          'the item has been removed from your watchlist!',
          'alert-success',
          setHelperText,
          3000
        );
        await dispatch(getWatchlist(login.id));
      }
    }
  };

  const handleAddToWatchlist = async () => {
    if (login && movieData) {
      const { data: wlData, error: wlError } = await supabase
        .from('watchlist')
        .select('*')
        .eq('user_id', login.id);
      if (wlError) console.error(wlError);

      let already = false;
      wlData.forEach(async (i) => {
        if (i.movie_id === movieData.id) {
          already = true;
        }
      });

      if (!already) {
        const { error: pushError } = await supabase
          .from('watchlist')
          .insert([{ movie_id: movieData.id, user_id: login.id }]);
        if (pushError) {
          helperFunction(
            false,
            pushError.message,
            'alert-error',
            setHelperText,
            3000
          );
        } else {
          helperFunction(
            false,
            'Movie added to your watchlist!',
            'alert-success',
            setHelperText,
            3000
          );
          await dispatch(getWatchlist(login.id));
        }
      } else {
        helperFunction(
          false,
          'The movie is already on your watchlist!',
          'alert-warning',
          setHelperText,
          3000
        );
      }
    }
  };

  useEffect(() => {
    (async function () {
      if (id) {
        await dispatch(getMovie(id));
        await dispatch(pageViewInc(id));
      }
      if (login) await dispatch(getWatchlist(login.id));
    })();
  }, [dispatch, id, login]);

  return (
    <div>
      {loading ? (
        <Loading />
      ) : (
        movieData && (
          <div>
            <button className='btn-inline' onClick={back}>
              <Back /> Back
            </button>

            <h2>{movieData.name}</h2>
            <p className='text-small'>
              Year: {movieData.year} - Views: {movieData.views || 0}
            </p>

            {!!helperText.text && (
              <Message data={helperText.text} type={helperText.type} />
            )}

            <div className='flex'>
              <div>
                <img
                  className='poster'
                  src={movieData.poster || poster}
                  alt={movieData.name}
                />
              </div>
              <div style={{ display: 'none' }} className='videoContainer'>
                <div className='videoWrapper'>
                  <iframe
                    title='YouTube video player'
                    frameBorder='0'
                    allowFullScreen
                    width='560'
                    height='349'
                    src={extractVideoId(movieData.trailer || '')}
                  ></iframe>
                </div>
              </div>
              <div>
                <Watchlist
                  login_id={login?.id}
                  item_id={movieData.id}
                  handleAddToWatchlist={handleAddToWatchlist}
                  handleRemoveToWatchlist={handleRemoveToWatchlist}
                  watchlist={watchlist}
                />
              </div>
            </div>
            <div className='App-tags mt-3'>
              {movieData.tags &&
                movieData.tags.map((x, i) => (
                  <Link to={`../genre/${x.trim()}`} key={i}>
                    <span>{x}</span>
                  </Link>
                ))}
            </div>
            <h3 className='mt-4 pb-3 before'>Storyline</h3>

            <div className='App-details'>
              <p>{movieData.storyline}</p>
            </div>

            <h3 className='mt-4 pb-3 before'>Details</h3>

            <div className='App-details'>
              <p>
                <b>Director:</b>
                {movieData.director}
              </p>

              <p>
                <b>Stars:</b>
                {arrayToString(movieData.cast)}
              </p>

              <p>
                <b>Year:</b>
                {movieData.year}
              </p>
            </div>
          </div>
        )
      )}
    </div>
  );
};

export default MoviePage;
