import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { supabase } from '../lib/api';
import { dateConvert } from '../helper';
import { getWatchlist } from '../store/user';
import { ReactComponent as OkIcon } from '../assets/icons2/extra/mdi_check-circle-outline.svg';
import { ReactComponent as NotOkIcon } from '../assets/icons2/w/mdi_check-circle-outline.svg';
import { ReactComponent as EyeIcon } from '../assets/icons2/mdi_eye-outline.svg';
import { ReactComponent as DelIcon } from '../assets/icons2/w/mdi_delete-outline.svg';
import { Link, useHistory } from 'react-router-dom';
import { ReactComponent as Back } from '../assets/icons2/mdi_arrow-left.svg';
import Loading from './Loading';
import Message from './Message';

const Profile = () => {
  const [message, setMessage] = useState(null);
  const dispatch = useDispatch();
  let history = useHistory();

  const { data: login } = useSelector((state) => state.user.login);
  const { data: watchlist, loading: watchlistLoading } = useSelector(
    (state) => state.user.watchlist
  );

  const back = () => history.goBack();

  const handleDelete = async (id) => {
    if (login && id) {
      const { data, error } = await supabase
        .from('watchlist')
        .delete()
        .eq('id', id)
        .eq('user_id', login.id);
      if (error) console.log(error);
      await dispatch(getWatchlist(login.id));
    }
  };

  const handleView = (id) => {
    console.log(id);
  };

  useEffect(() => {
    (async function () {
      await dispatch(getWatchlist(login.id));
    })();

    // TODO: get user profile
  }, [login, dispatch]);

  return (
    <div>
      <button className='btn-inline' onClick={back}>
        <Back /> Back
      </button>
      <div className='App-profile--user'>
        <h2>Profile</h2>
        <div className='p-4'>
          <div>{login?.id}</div>
          <div>
            <h4>{login?.email || 'Anonymous_'}</h4>
            <small>
              {login?.email} - Registration day {dateConvert(login?.created_at)}
            </small>
          </div>
        </div>
      </div>
      <div className='App-watchlist py-4'>
        {!!message && <Message data={message} type='alert-success' />}
      </div>
      <section>
        {!watchlistLoading ? (
          watchlist && (
            <table>
              <thead>
                <tr>
                  <th>id</th>
                  <th>movie id</th>
                  <th>i will see</th>
                  <th>favorite</th>
                  <th>options</th>
                </tr>
              </thead>
              {watchlist.map((i) => (
                <tbody key={i.id}>
                  <tr>
                    <td>{i.id}</td>
                    <td>{i.movie_id}</td>
                    <td>
                      <div className='flex flex-justify-center'>
                        {i.i_will_see ? <OkIcon /> : <NotOkIcon />}
                      </div>
                    </td>
                    <td>
                      <div className='flex flex-justify-center'>
                        {i.favorite ? <OkIcon /> : <NotOkIcon />}
                      </div>
                    </td>

                    <td>
                      <div className='flex flex-justify-center'>
                        <Link to={`/movies/${i.movie_id}`}>
                          <button
                            className='btn-icon btn-warning me-1'
                            onClick={() => handleView(i.id)}
                          >
                            <EyeIcon />
                          </button>
                        </Link>
                        <button
                          className='btn-icon btn-error'
                          onClick={() => handleDelete(i.id)}
                        >
                          <DelIcon />
                        </button>
                      </div>
                    </td>
                  </tr>
                </tbody>
              ))}
            </table>
          )
        ) : (
          <Loading />
        )}
      </section>
    </div>
  );
};

export default Profile;
