import React, { useEffect, useState } from 'react';
import { ReactComponent as AddIcon } from '../assets/icons2/w/mdi_bookmark-plus-outline.svg';
import { ReactComponent as WillIcon } from '../assets/icons2/w/mdi_calendar-clock.svg';
import { ReactComponent as FavIcon } from '../assets/icons2/w/mdi_star-outline.svg';
import { supabase } from '../lib/api';

const Watchlist = ({
  handleAddToWatchlist,
  handleRemoveToWatchlist,
  login_id,
  item_id,
  watchlist,
}) => {
  const [iWillSee, setIWillSee] = useState(false);
  const [favorite, setFavorite] = useState(false);
  const [loading, setLoading] = useState(false);
  const [item, setItem] = useState(null);

  const api = async (body) => {
    const { error } = await supabase
      .from('watchlist')
      .update(body)
      .eq('user_id', login_id)
      .eq('movie_id', item_id);
    if (error) console.log(error);
  };

  const onChangeWill = async () => {
    setLoading(true);
    await api({ i_will_see: !iWillSee });
    setIWillSee(!iWillSee);
    setLoading(false);
  };
  const onChangeFavorite = async () => {
    setLoading(true);
    await api({ favorite: !favorite });
    setFavorite(!favorite);
    setLoading(false);
  };

  useEffect(() => {
    if (watchlist) {
      let x = watchlist.filter((i) => i.movie_id === item_id);
      setItem(x[0]);
      if (item) {
        setIWillSee(item.i_will_see);
        setFavorite(item.favorite);
      }
    }
  }, [watchlist, item_id, item]);

  // if (!item) return null;
  return (
    <div className='App-watchlist mx-2'>
      {!item ? (
        <button
          className='btn-inline btn-info mb-2'
          onClick={handleAddToWatchlist}
          disabled={loading}
        >
          <AddIcon /> Add to watchlist
        </button>
      ) : (
        <>
          <button
            className='btn-inline btn-error mb-2'
            onClick={() => handleRemoveToWatchlist(item.movie_id)}
            disabled={loading}
          >
            <AddIcon /> Remove from watchlist
          </button>

          {!iWillSee ? (
            <button
              className='btn-inline btn-info mb-2'
              onClick={onChangeWill}
              disabled={loading}
            >
              <WillIcon /> I'll see later
            </button>
          ) : (
            <button
              className='btn-inline btn-error mb-2'
              onClick={onChangeWill}
              disabled={loading}
            >
              <WillIcon /> I already saw
            </button>
          )}
          {/*  */}
          {!favorite ? (
            <button
              className='btn-inline btn-info mb-2'
              onClick={onChangeFavorite}
              disabled={loading}
            >
              <FavIcon /> Favorite
            </button>
          ) : (
            <button
              className='btn-inline btn-error mb-2'
              onClick={onChangeFavorite}
              disabled={loading}
            >
              <FavIcon /> Remove Favorite
            </button>
          )}
        </>
      )}
    </div>
  );
};

export default Watchlist;
