import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import { getMovies } from '../store/movies';
import { ReactComponent as Back } from '../assets/icons2/mdi_arrow-left.svg';
import Card from './Card';
import Loading from './Loading';

const Movie = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const [type, setType] = useState('movie');
  const { data, loading } = useSelector((state) => state.movies.movies);

  const handleType = async () => setType(!type);
  const back = () => history.goBack();

  useEffect(() => {
    (async function () {
      await dispatch(getMovies());
    })();
  }, [dispatch]);

  return (
    <div>
      <button className='btn-inline' onClick={back}>
        <Back /> Back
      </button>
      <div>
        <h2>Movies</h2>
        <div className='flex wrap gap-1 flex-justify-center'>
          {loading ? (
            <Loading />
          ) : data ? (
            data
              .map((i) => (
                <Link to={`movies/${i.id}`} key={i.id} className='flex'>
                  <Card
                    name={i.name}
                    storyline={i.storyline}
                    date={i.year}
                    poster={i.poster}
                  />
                </Link>
              ))
              .reverse()
          ) : (
            <p>movies not found</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default Movie;
